.message{
    display: flex;
    flex-direction: column;
    margin-top: 20px;

}
.messageTop{
    display: flex;
}
.messageImg{
    width: 32px;
    height: 32px;
    border-radius:50px;
    object-fit: cover;
}


.messageText{
    padding: 10px;
    border-radius: 20px;
    background-color: blue;
    color:#fff;
    max-width: 300px;
}

.messageBottom{
    font-size: 12px;
    margin-top: 10px;

}

.message.own{
    align-items: flex-end;
}
.message.own .messageText{
    background-color: #eee;
    color: #000;
}